import  {getData} from '@/Api/ajax.js'
import { GETDETAIL,GETZOOMLIST} from '@/const/index'

const actions= {
 async getDetail(context,params){
    const result = await getData.getDetail(params)

    await context.commit(GETDETAIL,result)

    context.dispatch('getProductDetail',result.data?.product)
    
    context.dispatch('getProductPrice',result.data?.product)

 
  },

  getZoomList(context,params){
    const result = params.zoomList
    context.commit('GETZOOM',result)

  },
  async getPageDetail(context,params){
    const result = await getData.reqPageDetail()
    context.commit('GETPAGEDETAIL',result)

  }
  ,
  async getProductDetail(context,params){
      if(params){
      let obj = {
        'appid':'pc-item-soa',
        'functionId':'pc_detailpage_wareBusiness',
        't':String(Date.now()), //时间戳，          
        'client':'pc',
        'clientVersion':'1.0.0',
        'loginType':'3',
        'body':{  "skuId": params.skuid,
                  "cat": params.cat.toString(),
                  "area": "2_2825_61086_0",
                  "shopId": params.shopId,
                  "venderId": params.venderId,
                  "paramJson": params.paramJson,
                  "num": 1},
        'uuid':'122270672.1649466043856517667521.1649466043.1679903706.1679913541.43',
        'jsonp':'jQuery7123553',
        '_':String(Date.now())

      }

      let result = await getData.reqProductDetail(obj)

      

      if(result){
      
        context.commit('GETPRODUCTDETAIL',result)

      }


    }

  },
  async getProductPrice(context,params){
    if(params){
    let obj = {
      'appid':'pc-item-soa',
      'functionId':'pc_detailpage_wareBusiness',
      't':String(Date.now()), //时间戳，          
      'client':'pc',
      'clientVersion':'1.0.0',
      'loginType':'3',
      'body':{  "skuId": params.skuid,
                "cat": params.cat.toString(),
                "area": "2_2825_61086_0",
                "shopId": params.shopId,
                "venderId": params.venderId,
                "paramJson": params.paramJson,
                "num": 1},
      'uuid':'122270672.1649466043856517667521.1649466043.1679903706.1679913541.43',
      'jsonp':'jQuery7123553',
      '_':String(Date.now())

    }

    let result = await getData.reqProductPrice(obj)

    if(result){
      context.commit('GETPRODUCTPRICE',result)
    }


  }

}
}
const getters = {
  doneShop(state){
    let obj = state.pageDetail?.data.shopInfo.shop

    if(obj){

      let tmp = {
        'name':obj.name,
        'logo':obj.logo,
        'shopId':obj.shopId,
        'shopUrl':state.pageDetail.data.shopUrl
      }
      return tmp
    }
   return null

  }
}
const mutations = {
  GETDETAIL(state,result){
    state.product = result.data.product
    
  },
  GETZOOM(state,result){
    state.zoomList = result
  },
  GETPRODUCTDETAIL(state,result){
    state.productDetail = result
  },
  GETPRODUCTPRICE(state,result){
    state.productPrice = result
  },
  GETPAGEDETAIL(state,result){
    state.pageDetail = result

  }
}
const state = {'product':{}}

export default {actions, getters ,mutations ,state}