//home组件的store
import  {getData} from '@/Api/ajax.js'
import {GETSIDENAV,getCategory,getSideNav,GETCATEGORY ,GETBANNERSLIDER,flashSale, getFlashSale, GETFLASHSALE, getGoodsList, GETGOODSLIST} from '@/const'

const state = {
                sideNav:{},
                category:{},
                bannerSlider:{},
                flashSale:{},
                goodsList:[]
                
              }
const actions = { 
    async getSideNav(context){
        const data =  await getData.getSideNav()
      
        context.commit('GETSIDENAV',data)
    },
    async getCategory({commit}){

         const data = await getData.getCategory()
         commit('GETCATEGORY',data)
    },
    async getBannerSlider({commit}){
        const tmp =await getData.getBannerSlider()
        const data = tmp
        commit(GETBANNERSLIDER,data)

    },
    async getFlashSale({commit}){
        const data = await getData.getFlashSale()
        commit(GETFLASHSALE,data)
      
    },
    async getGoodsList({commit},params){
        
        const data = await getData.getGoodsList(params)
        commit(GETGOODSLIST,data)
    },
    async saveAddress({commit},params){
       commit('SAVEADDRESS',params)
    }

                        
}
const getters = {
    doneCategory(state){
        return 

    },
    doneBannerSlider(state){
        const tmp = []
        const data = state.bannerSlider.code||-1

        if(data==200){
            state.bannerSlider.data.data.forEach((el,index) => {
        
                tmp[index] = {}
                tmp[index].name = el[0].extension_id
                tmp[index].id = el[0].id
                tmp[index].src =el[0].src
                  
              });
        }
      
        return tmp
 
    },
    doneFlashSale(state){
        const tmp = []
        const data = state.flashSale.code||-1

        if(data==200){
            state.flashSale.data.indexMiaoSha.forEach((el,index) => {
        
                tmp[index] = {}
                tmp[index].name = el.wname
                tmp[index].id = el.activeId
                tmp[index].src =el.imageurl
                  
              });
        }
      
        return tmp
 
    },
   

 
}
const mutations = {
    GETSIDENAV(state,data){
        state.sideNav = data;
       
    },
    GETCATEGORY(state,data){
        state.category = data

    },
    GETBANNERSLIDER(state,data){
        state.bannerSlider = data

    },
    GETFLASHSALE(state,data){
        state.flashSale = data
    },
    GETGOODSLIST(state,data){
        state.goodsList = state.goodsList.concat(data)
    },
    SAVEADDRESS(state,data){
        state.address = data
        console.log('3');
    }

}
    
export default {state,actions,getters,mutations}
