<template>
  <div class="type-nav" @mouseenter="getEnter" @mouseleave="getOut">
            <div class="container" v-if="status.sideNav" @click="getSearch">
                <h2 class="all" >{{sideNav.data.item}}</h2> 
                <nav class="nav" >
                    <a :href="nav.href" v-for="(nav) in sideNav.data.nav" :key="nav.id">{{nav["item-name"]}}</a>
                </nav>
                <div class="sort" v-show="status.sort">
                    <div class="all-sort-list2">
                        <div class="item bo" v-for="(tapeNav,index) in sideNav.data['tape-nav']" :key="index">
                            <h3>
                                <a href="" v-for="(item) in tapeNav" :key="item.id" :data-index="index" @mouseover="getCurrent">{{item['item-name']}}
                                  
                                </a>
                                
                            </h3>
                            <div class="item-list clearfix" v-if="status.category">
                                <div class="subitem" >
                                    <dl class="fore"  v-for="(curCategory,index) in curCategory" :key="index">
                                        <dt>
                                            <a >{{curCategory.name}}</a>
                                        </dt>
                                        <dd>
                                            <em v-for="(subNav,subIndex) in curCategory.subNav" :key="subIndex" >
                                                <a href="">{{subNav.name}}</a>
                                            </em>

                                        </dd>
                                    </dl>
                                </div>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </div>
  </div>
</template>

<script>
import { ref,reactive } from '@vue/reactivity'
import { useRouter ,useRoute} from 'vue-router'
import { onMounted,computed, nextTick, watchEffect,watch, onBeforeMount } from '@vue/runtime-core'
import { mapState, useStore } from 'vuex'
import { getSideNav } from '@/const'




export default {
    setup(){
      
       const route = useRoute()
       const router = useRouter()
       const store = useStore()
    
       store.dispatch({type:getSideNav})

       let show = ref(false)
       onBeforeMount(()=>{
            if(route.fullPath=='/home'){
                show.value= true
            }else{
              status.sort = false
            }
            if(/^\/search/.test(route.fullPath)){
              console.log('这里是search页面');
              status.sort= false
            }
      
        }
       )
     

       function getEnter(){
       
            if(!/^\/home/.test(route.fullPath)){
                show.value = true
                status.sort = true
                }
        
        }

       function getOut(){
        
            if(!/^\/home/.test(route.fullPath)){
            show.value = false
            status.sort = false
        } 
        
       }
     
       function getCurrent(e){
        if(!status.category){store.dispatch("getCategory")}
         status.curIndex = e.target.dataset.index
       }

       function trimString(str=""){
            const arr = str.split('|')
            const href = arr[0]
            const name = arr[1]
            return {href,name}

        }

        function getSearch(e){
          e.preventDefault()
          router.push({name:'search',query:{'search':e.target.innerText,'page':'1'}})
      
        }
        const pp = {'name':'jack'}
        const try1 =computed((()=>{return pp.name}))
        const sideNav = computed(()=>{return store.state.home.sideNav})
        const category = computed(()=>{ return store.state.home.category})
        const status= reactive({"sideNav":false,"category":false,"curIndex":-1,'sort':true})
        const curCategory = computed(()=>{
            if(status.category){

              const tmp = category.value.data.data.find((element,index)=>index==status.curIndex)
              const obj = []
              tmp.s[0].s.forEach((element,index) => { 
                obj[index] = {'name':trimString(element.n).name,
                              'href':trimString(element.n).href,
                              'subNav':function(){
                                 const subArrr = []
                                 element.s.forEach((subElement,subIndex)=>{
                                   subArrr[subIndex] = {
                                    'name':trimString(subElement.n).name,
                                    'href':trimString(subElement.n).href
                                   }
                                 })
                                 return subArrr
                              }()
              }
              
                
              });
              // 遍历tmp对象并将数据转化为正确的数据
              return obj
             }else{return {}}
             
            })
        watch(sideNav,()=>{if(store.state.home.sideNav.code==200){;status.sideNav=true;}})
        watch(category,()=>{if(store.state.home.category.code==200){ status.category=true}})

      
        
        return{
            show,
            getEnter,
            getOut,
            getCurrent,
            getSearch,
            trimString,
            status,
            sideNav,
            category,
            curCategory
           
        }
    }

}
</script>

<style>
 .type-nav {
  border-bottom: 2px solid #e1251b;
}
 .type-nav .container {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  position: relative;
}
 .type-nav .container .all {
  width: 210px;
  height: 45px;
  background-color: #e1251b;
  line-height: 45px;
  text-align: center;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
}
 .type-nav .container .nav a {
  height: 45px;
  margin: 0 10px;
  line-height: 45px;
  font-size: 16px;
  color: #333;
}
 .type-nav .container .sort {
  left: 0;
  top: 45px;
  width: 210px;
  height: 461px;
  position: absolute;
  background: #fafafa;
  z-index: 999;
}
 .type-nav .container .sort .all-sort-list2 .item h3 {
  line-height: 25px;
  font-size: 14px;
  font-weight: 400;
  overflow: hidden;
  padding: 0 20px;
  margin: 0;
}
 .type-nav .container .sort .all-sort-list2 .item h3 a {
  color: #333;
  margin-right: 2px;
}
 .type-nav .container .sort .all-sort-list2 .item .item-list {
  display: none;
  position: absolute;
  width: 734px;
  min-height: 460px;
  background: #f7f7f7;
  left: 210px;
  border: 1px solid #ddd;
  top: 0;
  z-index: 9999 !important;
}
 .type-nav .container .sort .all-sort-list2 .item .item-list .subitem {
  float: left;
  width: 1000px;
  padding: 0 4px 0 8px;
}
 .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl {
  border-top: 1px solid #eee;
  padding: 5px 0;
  overflow: hidden;
  zoom: 1;
}
 .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl.fore {
  border-top: 0;
}
 .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl dt {
  float: left;
  width: 54px;
  line-height: 22px;
  text-align: right;
  padding: 3px 6px 0 0;
  font-weight: 700;
}
 .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl dd {
  float: left;
  width: 700px;
  padding: 3px 0 0;
  overflow: hidden;
}
 .type-nav .container .sort .all-sort-list2 .item .item-list .subitem dl dd em {
  float: left;
  height: 14px;
  line-height: 14px;
  padding: 0 8px;
  margin-top: 5px;
  border-left: 1px solid #ccc;
}
 .type-nav .container .sort .all-sort-list2 .item:hover .item-list {
  display: block;
}

</style>