import  {getData} from '@/Api/ajax.js'
import { GETDETAIL,GETZOOMLIST} from '@/const/index'
import myFetch from '@/Api/myFetch'

const state = {message:{}}
const actions = {
    async loginSubmit({commit},params){
        let response = await  myFetch('http://localhost:8080/proxy/login',{method:'post',data:JSON.stringify(params)})
        
        console.log(response,'actions',typeof response)
        commit('LOGIN',response)
    }
}
const mutations={
    LOGIN(state,params){
        state.message = params
    }
}
export default {state,actions,mutations};