
import mockRequests from './mockRequests'
import axios from 'axios'
import requests from './mockRequests'
export const getData= {
    async getSideNav(){
        const data = await mockRequests({url:'/sideNav',method:'post'})
        return data
    },
    getCategory(){
        return mockRequests({url:'/category',method:'post'})
    },
    async getBannerSlider(){
        const data = await mockRequests({url:'/bannerSlider',method:'post'})
        return data
    },
    async getFlashSale(){
        const data = await mockRequests({url:'/flashSale',method:'post'})
        return data
    },
    async getGoodsList(item){
       
        const res =  await axios.get('http://localhost:5566/home/goodsList',{params:{'pages':item.pages,'times':123123}})
        return res.data.data
    },
    async getSearchList(item){
        console.log(item,'ajax');
        const res = await  axios.post('http://localhost:5566/search',{'page':item.page,'search':item.search,'callback':'attention'})
        return res
    },
    async getDetail(item){

        console.log('detail的axios已经发送');
        const res = await axios.get('http://localhost:5566/detail',{params:{'url':item.url}})

        //这里书写mock接口
        return res
    },
    async reqProductDetail(params){
       let res = await axios.get('http://localhost:5566/detail/productDetail',
            {
                params:{
                    'reqParams':params,
                    'url':'https://api.m.jd.com/api'
                }
            }
       ) .catch((err) => { console.log(err.toJSON()); return false})
       
       console.log(typeof res.data);
       return (res.data)
 

    },
    async reqProductPrice(params){
      let res = await axios.get('http://localhost:5566/detail/productPrice',
            {
                params:{
                    'reqParams':params,
                    'url':'https://api.m.jd.com/api'
                }
            }
       )
        .catch((err) => { console.log(err.toJSON()); return false})

      return res.data

       
    },
    async reqPageDetail(params){
        let res = await axios.get('http://localhost:5566/detail/pageDetail',{params:{}})
        return res
    },
    async reqPayFor(params){
        let res = await requests({mathod:'post',url:'http://localhost:5566/payFor',data:params})
        
        return res
    }
}
