//myFetch.js 封装fetch
import {store} from'@/store/index'
import doCookies from './doCookie'

const originFetch = window.fetch


let requestHandlers = [] //对请求的处理方法，收集在数组中
let responseHandlers = []

let interceptors = {
    request:{
        use:(callback,errCallback) => {
            requestHandlers.push(callback)
        }
    },
    response:{
        use:(callback) => {
            responseHandlers.push(callback)
        }
    }
}
//拦截请求的文件格式没有操作，JSON.stringify,表单根据ectype属性，字符就json化，文档好像fetch会自动转换为流的形式
interceptors.request.use((config) => {
    let token  = doCookies.getItem('access_token')
    let userId = doCookies.getItem('userId')
    if(token){
        config ={
            headers:{
                access_token:token
            },
            ...config,

        }
    }
    if(userId){
        config = {
            headers:{
                userId:userId,
                access_token:token   
            }
        }
    }
    
    return config

})
interceptors.response.use(async (response) => {
    if(response.ok&&response.status==200){
        let type = response.headers.get('Content-type')
       let  token = response.headers.get('access-token')
        
        if(/json/.test(type)){
             response =  await response.json() 
             return response
        }
        if(/text/.test(type)){
            response = await response.text()
            console.log(response,'intercaptors response');
            return response
        }
        if(/formData/.test(type)){
            response = await response.formData()
            return response
    
        }
        if(/blob/.test(type)){
            response = await response.blob()
            return response
        }
        return await response.json()

    }else{
        console.log('响应拦截失败，没有处理函数');
        return Promise.reject(response)
    }
    
})


let myFetch = (url,config) => {
   
 //遍历请求设置的方法   
    requestHandlers.forEach((item) => {
        config = item(config)   
    })


    return new Promise((resolve,reject) => {

        let response = originFetch(url,config)
            .then((response) => {
                let res = response.clone()
                //遍历响应处理的方法
                responseHandlers.forEach((item) => {
                   res = item(res)
                    
                })

                resolve(res)
                          
            })
            .catch((err) => {
                reject(err)
                
            })

        
       
    })

}

export default myFetch;