import { getData } from "@/Api/ajax"
const state = {
    

}

const actions = {
    saveReqPayForData({commit},params){
        commit('SAVEPAYFOR',params)

    }
}

const mutations = {
    SAVEPAYFOR(state,data){
        //储存支付页面所携带的数据
        state.reqPayforData = data

    }
}

export default{state,actions,mutations}