import {getData} from '@/Api/ajax.js'
import {getSearchList,GETSEARCHLIST} from '@/const/index.js'
const _ = require('lodash')
const state = {
    searchList:[]
}
const actions = {
    async getSearchList({commit},params){
        const  data = await getData.getSearchList(params)
        console.log(data,'action中的data');
        commit(GETSEARCHLIST,data)

    }

}
const getters = {
    doneSearchList(state,params1,params2){
        console.log(params1,params2,'打印store中的getters参数');
        return (params)=>{
            
            let sortList = []
            let dataList = []
            
            const code = state.searchList.status||-1
            console.log(code);
            if(code == 200){
              let cloneList = _.cloneDeep(state.searchList.data.searchList)
              switch(params.sort){
                case '0':
                    sortList = state.searchList.data.searchList
                    break;
                case '1':
                    sortList = cloneList.sort(
                        (a,b)=>{return a.price - b.price}
                    )
                    break;
                case '-1':
                    sortList = cloneList.sort(
                        (a,b)=>{return b.price - a.price}
                    )
                    break;
                case '4':
                    for(let element of cloneList){
                        if(element['p-commit'].search('万')!==-1){
                            let str = element['p-commit'].split('万')[0] + '0000'
                            element['p-commit'] = str
                            
                    
                    
                        }
                    
                      cloneList.sort((a,b)=>{return b['p-commit']-a['p-commit']})
                    }
                      for(let element of cloneList){
                        if(element['p-commit']>=10000){
                           let  str = element['p-commit'].slice(0,-4) + '万'
                          element['p-commit']  = str
                        }
                      }
                    console.log(cloneList,'这里石克隆');
                    sortList = cloneList
                    break;
                default:
                    sortList = state.searchList.data.searchList
                    break;

            }
              let end = params['page-size'] * params['page']
              let start = end - params['page-size']
              dataList = sortList.slice(start,end)
            }
            
            return  dataList
        }

    }
}
const mutations = {
    GETSEARCHLIST(state,data){
        state.searchList = data

    }
}

export default {state,actions,mutations,getters}
